import multidata from "../../public/data/home/multidata.json";
import pop from "../../public/data/home/pop.json";
import news from "../../public/data/home/new.json";
import sell from "../../public/data/home/sell.json";

export function getMultidata() {
  return new Promise((resolve, reject) => {
    resolve(multidata);
  });  
}

export function getHomeGoods(type, page) {
  if (page > 3) {
    page = page % 3;
    if (page == 0) {
      page = 3;
    }
  }
  if (type === 'pop') {
    return new Promise((resolve, reject) => {
      resolve(pop[page]);
    });
  } else if (type === 'new') {
    return new Promise((resolve, reject) => {
      resolve(news[page]);
    });
  } else if (type === 'sell') {
    return new Promise((resolve, reject) => {
      resolve(sell[page]);
    });
  }
}

