<template>
  <div id="home">
    <nav-bar class="home-nav"><span slot="center">购物街</span></nav-bar>
    <tab-control :titles="['流行', '新款', '精选']" @tabClick="tabClick" 
      class="tab-control" v-show="isTabControlFixed" ref="tabControlFixed" />
    <scroll class="scroll-content" ref="scroll" :probe-type="3" :pullUpLoad="true"
      @scroll="contentScroll" @pullingUp="loadMoreGoods">
      <home-swiper :banners="banners" @swiperImageLoad="swiperImageLoad" />
      <recommend-view :recommends="recommends" />
      <feature-view />
      <tab-control :titles="['流行', '新款', '精选']" @tabClick="tabClick" ref="tabControl" />
      <goods-list :goods="goods[currentType].list" />
    </scroll>
    <back-top class="back-top" @click.native="backTopClick" v-show="isShowBackTop" />
  </div>
</template>

<script>
// common
import { backTopMixin } from "common/mixin";

// 公共组件文件
import NavBar from "components/common/navbar/NavBar";
import TabControl from "components/content/tabControl/TabControl";
import GoodsList from "components/content/Goods/GoodsList";
import Scroll from "components/common/scroll/Scroll";
import BackTop from "components/content/backtop/BackTop";

// 子组件文件
import HomeSwiper from "./childComps/HomeSwiper";
import RecommendView from "./childComps/RecommendView";
import FeatureView from "./childComps/FeatureView";

// 三方框架库
import BScroll from 'better-scroll';
import { getMultidata, getHomeGoods } from "network/home";
import { debounce } from "common/debounce";

export default {
  components: {
    NavBar,
    TabControl,
    GoodsList,
    Scroll,
    BackTop,
    HomeSwiper,
    RecommendView,
    FeatureView,
  },
  data() {
    return {
      banners: [],
      recommends: [],
      goods: {
        pop: {page: 0, list: []},
        new: {page: 0, list: []},
        sell: {page: 0, list: []},
      },
      currentType: 'pop',
      tabControlTop: 0,
      isTabControlFixed: false,
      saveY: 0,
      itemImgListener: null
    }
  },
  mixins: [backTopMixin],
  created() {
    this.getHomeMultidata();

    this.getHomeGoods('pop');
    this.getHomeGoods('new');
    this.getHomeGoods('sell');
  },
  updated() {
    this.$refs.scroll.refresh();
  },
  activated() {
    this.$refs.scroll.refresh();
    this.$refs.scroll.scrollTo(0, this.saveY, 0);
  },
  deactivated() {
    this.saveY = this.$refs.scroll.scroll.y;
  },
  methods: {
    /*
      事件监听
     */
    tabClick(index) {
      switch (index) {
        case 0:
          this.currentType = 'pop';
          break;
        case 1:
          this.currentType = 'new';
          break;
        case 2:
          this.currentType = 'sell';
          break;
      }
      this.$refs.tabControl.currentIndex = index;
      this.$refs.tabControlFixed.currentIndex = index;
    },
    contentScroll(position) {
      // 显示返回顶部按钮
      this.isShowBackTop = (-position.y) > 1000 ? true : false;
      // 吸顶效果
      this.isTabControlFixed = (-position.y) > this.tabControlTop;
    },
    loadMoreGoods() {
      this.getHomeGoods(this.currentType);
    },
    swiperImageLoad() {
      this.tabControlTop = this.$refs.tabControl.$el.offsetTop;
    },
    /*
      网络请求相关方法
    */
    // 加载推荐数据
    getHomeMultidata() {
      getMultidata().then(res => {
        this.banners = res.data.banner.list;
        this.recommends = res.data.recommend.list;
      });
    },
    // 加载列表商品数据
    getHomeGoods(type) {
      const page = this.goods[type].page + 1;
      getHomeGoods(type, page).then(res => {
        this.goods[type].list.push(...res.data.list);
        this.goods[type].page += 1;
        // 数据请求完，刷新上拉加载更多
        this.$refs.scroll.finishPullUp();
      });
    }    
  },
}
</script>

<style>
  .home-nav {
    background-color: var(--color-tint);
    color: #fff;
  }

  .scroll-content {
    position: absolute;
    top: 44px;
    left: 0;
    right: 0;
    bottom: 49px;
    overflow: hidden;
  }

  .tab-control {
    position: relative;
    z-index: 9;
  }
</style>